:root{--education-title-color: #ffffff;--education-text-color: #ffffff;--education-subtext-color: rgba(255, 255, 255, 0.8);--education-description-color: rgba(255, 255, 255, 0.9);--education-highlight-color: #ffffff;--education-scroll-icon-color: #ffffff;--education-block-icon-color: #2563eb}.light-mode{--education-title-color: #000000;--education-text-color: #000000;--education-subtext-color: rgba(0, 0, 0, 0.8);--education-description-color: rgba(0, 0, 0, 0.9);--education-highlight-color: #000000;--education-scroll-icon-color: #000000;--education-block-icon-color: #000000}.light-mode .education-icon,.light-mode .location-icon,.light-mode .fa-university,.light-mode .fa-school,.light-mode .fa-graduation-cap,.light-mode .fa-map-marker-alt{color:#000 !important}.education-container{padding:80px 5%;max-width:1200px;margin:0 auto;position:relative}.education-container h1{text-align:center;margin-bottom:3rem;font-size:2.5rem;color:var(--education-title-color);font-weight:600;text-transform:uppercase;letter-spacing:2px;position:relative;z-index:2}.education-scroll-container{position:relative;display:flex;align-items:center;gap:20px}.education-grid{display:flex;gap:30px;overflow-x:auto;scroll-behavior:smooth;padding:20px 0;scrollbar-width:none;-ms-overflow-style:none}.education-grid::-webkit-scrollbar{display:none}.education-card{min-width:350px;background:hsla(0,0%,100%,.05);border-radius:15px;padding:30px;box-shadow:0 10px 30px rgba(0,0,0,.3);border:1px solid hsla(0,0%,100%,.1);transition:all .3s ease;position:relative}.education-card:hover{transform:translateY(-10px);box-shadow:0 20px 40px rgba(0,0,0,.4)}.education-duration{position:absolute;top:20px;right:20px;background:#2563eb;color:#fff;padding:8px 12px;border-radius:20px;font-size:.85rem;font-weight:600;display:flex;align-items:center;gap:5px}.education-duration .duration-icon{font-size:.8rem;color:#fff}.education-header{display:flex;align-items:flex-start;gap:20px;margin-bottom:20px;margin-top:20px}.education-icon{color:var(--education-block-icon-color);margin-top:5px}.education-card .education-icon,.education-card .location-icon{color:var(--education-block-icon-color)}.education-title{flex:1}.education-title h3{color:var(--education-title-color);font-size:1.4rem;font-weight:600;margin-bottom:5px;line-height:1.3}.education-title h4{color:var(--education-subtext-color);font-size:1.1rem;font-weight:500;margin:0}.education-institution{margin-bottom:15px}.education-institution h4{color:var(--education-title-color);font-size:1.2rem;font-weight:600;margin-bottom:8px}.education-location{display:flex;align-items:center;gap:8px;color:var(--education-subtext-color);font-size:.95rem}.education-location .location-icon{font-size:.85rem;color:var(--education-block-icon-color)}.education-grade{background:linear-gradient(135deg, #2563eb, #3b82f6);color:#fff;padding:10px 15px;border-radius:10px;font-weight:600;text-align:center;margin-bottom:20px;font-size:.95rem}.education-description{color:var(--education-description-color);font-size:1rem;line-height:1.6;margin-bottom:20px}.education-highlights{list-style:none;padding:0;margin:0}.education-highlights li{position:relative;color:var(--education-highlight-color);font-size:.95rem;line-height:1.5;margin-bottom:8px;padding-left:20px}.education-highlights li::before{content:"✓";position:absolute;left:0;color:#2563eb;font-weight:bold}.scroll-button{background:hsla(0,0%,100%,.05);border:2px solid hsla(0,0%,100%,.1);border-radius:50%;width:50px;height:50px;display:flex;align-items:center;justify-content:center;cursor:pointer;transition:all .3s ease;color:var(--education-scroll-icon-color);z-index:1}.scroll-button:hover{background:#2563eb;color:#fff;border-color:#2563eb;transform:scale(1.1)}.scroll-button.left{margin-right:10px}.scroll-button.right{margin-left:10px}@media(max-width: 768px){.education-container{padding:60px 3%}.education-container h1{font-size:2rem;margin-bottom:2rem}.education-card{min-width:280px;padding:20px}.education-header{gap:15px}.education-title h3{font-size:1.2rem}.education-title h4{font-size:1rem}.scroll-button{width:40px;height:40px}}@media(max-width: 480px){.education-scroll-container{flex-direction:column;gap:15px}.education-grid{gap:20px}.education-card{min-width:250px}.scroll-button{display:none}}