:root{--activity-title: #ffffff;--activity-text: #cbd5e0;--activity-date: #2563eb;--activity-link: #2563eb;--activity-background: rgba(255, 255, 255, 0.05);--scroll-button: rgba(255, 255, 255, 0.1);--scroll-button-hover: rgba(255, 255, 255, 0.2);--scroll-icon: #ffffff}.light-mode{--activity-title: #1a202c;--activity-text: #2d3748;--activity-date: #2563eb;--activity-link: #2563eb;--activity-background: rgba(255, 255, 255, 0.9);--scroll-button: rgba(0, 0, 0, 0.1);--scroll-button-hover: rgba(0, 0, 0, 0.2);--scroll-icon: #000000}.extracurricular-container{padding:4rem 2rem;position:relative}.extracurricular-container h1{text-align:center;margin-bottom:3rem;color:var(--activity-title);font-size:2.5rem;font-weight:600;text-transform:uppercase;letter-spacing:2px;position:relative;z-index:2}.activities-scroll-container{position:relative;max-width:1400px;margin:0 auto;padding:0 40px}.activities-grid{display:flex;overflow-x:scroll;scroll-behavior:smooth;scrollbar-width:none;-ms-overflow-style:none;gap:2rem;padding:1rem 0}.activities-grid::-webkit-scrollbar{display:none}.activity{flex:0 0 calc(33.333% - 1.33rem);min-width:300px;padding:1.5rem;border-radius:10px;transition:transform .3s ease;background:var(--activity-background)}.activity:hover{transform:translateY(-5px)}.activity-duration{color:var(--activity-date);font-size:.9rem;margin-bottom:1rem;font-weight:500;background:none;padding:0;border-radius:0;display:block;text-align:left;box-shadow:none;letter-spacing:normal}.activity-header{display:flex;align-items:center;margin-bottom:1rem;gap:15px}.activity-header h3{color:var(--activity-title);margin:0;font-size:1.5rem;line-height:1.4}.activity-icon{color:var(--activity-date);min-width:40px}.activity-description{color:var(--activity-text);margin-bottom:1rem;line-height:1.6;font-size:1rem}.activity-list{list-style:none;padding:0;margin:0}.activity-list li{padding:8px 0;padding-left:20px;position:relative;color:var(--activity-text);font-size:.95em}.activity-list li:before{content:"▸";position:absolute;left:0;color:var(--activity-date);font-weight:bold}.activity-list li:hover{color:var(--activity-title);transition:color .2s ease}.scroll-button{position:absolute;top:50%;transform:translateY(-50%);background:var(--scroll-button);border:none;border-radius:50%;width:40px;height:40px;display:flex;align-items:center;justify-content:center;cursor:pointer;transition:background-color .3s ease;z-index:1}.scroll-button:hover{background:var(--scroll-button-hover)}.scroll-button.left{left:0}.scroll-button.right{right:0}.scroll-button svg{color:var(--scroll-icon)}@media(max-width: 768px){.activity{flex:0 0 calc(100% - 2rem)}}